.content {
  display: flex;
  flex-direction: column;
  border-left: 3px solid var(--color-primary-400);;
  margin: 10px;
  margin-bottom: 20px;
  padding-left: 10px;
  align-items: flex-start;
}

h3 {
  font-size: 20px;
  margin-top: 0;
  margin-bottom: 5px;
}

h5 {
  margin: 0;
  font-size: 18px;
  color: var(--color-success-500);
}

span {
  font-size: 16px;
  color: var(--color-grey-700);
  margin-left: 5px;
}

p {
  font-size: 16px;
}
