.outer {
  width: 150px;
  height: 150px;
  border-radius: 100%;
  overflow: hidden;
  margin: 10px;
  position: relative;
}

.inner {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100%;
  border: 10px solid var(--color-primary-100);
  border-radius: 100%;
}

.block {
  width: 75px;
  height: 75px;
  position: absolute;
  overflow: hidden;
  z-index: 100;
}

.block-0 {
  right: 0;
  top: 0;
  border-top: 10px solid var(--color-primary-400);
  border-right: 10px solid var(--color-primary-400);
  border-top-right-radius: 100%;
}

.block-1 {
  right: 0;
  bottom: 0;
  border-right: 10px solid var(--color-primary-400);
  border-bottom: 10px solid var(--color-primary-400);
  border-bottom-right-radius: 100%;
}

.block-2 {
  left: 0;
  bottom: 0;
  border-bottom: 10px solid var(--color-primary-400);
  border-left: 10px solid var(--color-primary-400);
  border-bottom-left-radius: 100%;
}

.block-3 {
  left: 0;
  top: 0;
  border-left: 10px solid var(--color-primary-400);
  border-top: 10px solid var(--color-primary-400);
  border-top-left-radius: 100%;
}

.text,
.level {
  text-transform: uppercase;
}

.text {
  font-weight: bold;
  font-size: 16px;
}

.level {
  color: var(--color-primary-200);
  font-size: 14px;
}

