.skill {
  padding: 5px 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.skill span {
  font-weight: bold;
  margin-left: 10px;
}

.level {
  display: inline-block;
  margin-right: 20px;
}

.yes,
.no {
  display: inline-block;
  width: 15px;
  height: 15px;
  border-radius: 100%;
  border: 1px solid var(--color-primary-400);
}

.yes {
  background-color: var(--color-primary-400);
  margin-top: 10px;
  margin-right: 2px;
}

.no {
  display: inline-block;
  width: 15px;
  height: 15px;
  border-radius: 100%;
  margin-top: 10px;
  margin-right: 2px;
}
