.header {
  background-color: var(--color-primary-400);
  padding: 20px 0;
}

.avatar {
  display: flex;
  justify-content: flex-end;
  flex-direction: column;
}

.url,
.url a,
.title,
.avatar h2 {
  color: var(--color-grey-50);
}

.avatar h2 {
  margin: 0;
  margin-top: 10px;
}

.title {
  font-size: 26px;
}

.url {
  padding-top: 10px;
}

.url a {
  padding-left: 10px;
}

.icon {
  font-size: 36px;
  color: "#90a955";
}
